.cursor-follow{z-index:999;position:fixed;background:#1a1a1ab7;width:0.5em;height:0.5em;border-radius:50%;pointer-events:none;transform:translate(-50%, -50%)}.cursor-main{z-index:999;position:fixed;background:#9e9d9d;box-shadow:0 0 10px #9e9d9d, 0 0 60px #9e9d9d, 0 0 15px #9e9d9d;opacity:0.3;border-radius:50%;pointer-events:none;transform:translate(-50%, -50%);transition:0.3s}.cursor-follow,.cursor-main{display:none}@media (min-width: 1280px){.cursor-follow,.cursor-main{display:block}}
